    var(--cds-field-01, var(--color-grey-225-10-97))
  );
  --input-border-color: var(
    --cds-border-strong,
    var(--cds-border-strong-01, var(--color-grey-225-10-75))
  );
  --input-focus-background-color: var(
    --cds-field,
    var(--cds-field-01, var(--color-blue-205-100-95))
